import template from './chart.template';

const defaultOptions = {
  percent: 0,
  duration: 1000,
  frame: 30,
}

/**
 * Chart í´ëž˜ìŠ¤
 */
class Chart {
  #template = template;
  #el;
  #percent;
  #duration;
  #label;
  #frame;
  #handle;

  /**
   * 
   * @param {string} container - ë§ˆìš´íŠ¸ë  DOM ì»¨í…Œì´ë„ˆ ì…€ë ‰í„°
   * @param {string} data - ì˜µì…˜ ë°ì´íƒ€ duration, frame
   * @example
   * new Chart('#root', {
   *   duration: 2000,
   *   frame: 20,
   * });
   */
  constructor(container, data = {}) {
    const { duration, frame, percent } = {...defaultOptions, data};

    this.#duration = duration;
    this.#frame = frame;
    this.#percent = percent;

    this.#el = document.querySelector(container);
  }

  /**
   * í¼ì„¼íŠ¸ë¥¼ ì„¤ì •í•©ë‹ˆë‹¤.
   */
  set percent(per) {
    this.#percent = per;
  }

  /**
   * ì• ë‹ˆë©”ì´ì…˜ ì‹œê°„ì„ ms ë‹¨ìœ„ë¡œ ì„¤ì •í•©ë‹ˆë‹¤.
   */
  set duration(dur) {
    this.#duration = dur;
  }

  /**
   * ì—ë‹ˆë©”ì´ì…˜ì´ ì‹¤í–‰ ë  ë•Œ ì´ˆë‹¹ í”„ë ˆìž„ì„ ì„¤ì •í•©ë‹ˆë‹¤.
   */
  set frame(fr) {
    this.#frame = fr;
  }

  set label(text) {
    this.#label = text;
  }
  /**
   * ì†ŒìŠ¤ ë°ì´íƒ€ë¥¼ ì„¤ì •í•©ë‹ˆë‹¤.
   * @param {Array} source - 2ì°¨ì› ë°°ì—´ë¡œ ì œê³µí•©ë‹ˆë‹¤.
   */
  setDataSource(source) {

  }
  /**
   * UI ì—…ë°ì´íŠ¸ë¥¼ ìˆ˜í–‰í•©ë‹ˆë‹¤.
   */
  render() {
    this.#el.innerHTML = this.#template({
      percent: this.#percent * 10,
      duration: `${this.#duration / 1000}s`,
      label: this.#label,
    });

    const maxLoop = Math.floor(this.#duration / (1000/this.#frame));
    let loopCount = 0;

    this.#handle = setInterval(() => {
      loopCount++;

      this.#el.querySelector('#progress').innerHTML 
        = loopCount > maxLoop ? `${this.#percent}%` : `${Math.floor(this.#percent / maxLoop) * loopCount}%`;

      if (loopCount > maxLoop) {
        clearInterval(this.#handle);
      }
    }, 1000/this.#frame);
  }
}
 
export default Chart;